import React, { useState } from "react";
import ItemsCarousel from "react-items-carousel";

import { useTheme, withStyles } from "@material-ui/core/styles";
import { useMediaQuery, Button } from "@material-ui/core";

import FlowerItem from "../../Products/Components/FlowerItem";
import history from "./history";

const styles = {
    tr: {
        background: "#fff",
        marginTop: 20,
        marginBottom: 20,
        textAlign: "center",
        "&:hover": {
            boxShadow: "0px 25px 100px -57px rgba(0,0,0,0.75)",
        },
    },
    titleContainer: {
        width: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        padding: "5rem 30rem 5rem 30rem",
    },
};

function MiniCarousel(props) {
    const [activeItemIndex, setActiveItemIndex] = useState(0);

    const chevronWidth = 60;

    const data = props.data;
    const carousel = props.data.carousel;

    const theme = useTheme();
    const tablet = useMediaQuery(theme.breakpoints.up("md"));
    const mobile = useMediaQuery(theme.breakpoints.up("sm"));
    const web = useMediaQuery(theme.breakpoints.up("lg"));

    return (
        <div
            style={{
                padding: "20px 55px",
                maxWidth: 1000,
                margin: "0 auto",
                overflow: "hidden",
            }}
        >
            <div
                style={{
                    width: "100%",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                }}
            >
                <img
                    src={data.image}
                    alt="Tsetseg title"
                    style={{ width: "60%", minWidth: "300px" }}
                />
            </div>
            <ItemsCarousel
                infiniteLoop={true}
                activePosition={"center"}
                requestToChangeActive={setActiveItemIndex}
                activeItemIndex={activeItemIndex}
                numberOfCards={web ? 4 : tablet ? 3 : mobile ? 3 : 2}
                gutter={100}
                leftChevron={
                    <div
                        style={{
                            fontSize: 50,
                            color: "#AAA",
                        }}
                    >
                        {"<"}
                    </div>
                }
                rightChevron={
                    <div
                        style={{
                            fontSize: 50,
                            color: "#AAA",
                        }}
                    >
                        {">"}
                    </div>
                }
                outsideChevron
                chevronWidth={chevronWidth}
            >
                {carousel.map((item, idx) => {
                    return <FlowerItem data={item} key={idx} />;
                })}
            </ItemsCarousel>
            <div
                style={{
                    width: "100%",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    flexDirection: "column",
                    textAlign: "center",
                }}
            >
                <p style={{ width: "70%", minWidth: 300, fontSize: 18 }}>
                    {data.description}
                </p>
                <Button
                    style={{
                        backgroundColor: "#83d905",
                        borderRadius: 20,
                        color: "white",
                        height: 30,
                    }}
                    onClick={() =>
                        history.push(`/ProductScreen`, window.scrollTo(0, 0))
                    }
                >
                    Дэлгэрэнгүй
                </Button>
            </div>
        </div>
    );
}

export default withStyles(styles)(MiniCarousel);
