import React from "react";
import { Grid, Button } from "@material-ui/core";
import wow1 from "../../../assets/Images/wow1.jpg";
import wow2 from "../../../assets/Images/wow2.jpg";
import gadna from "../../../assets/Images/gadna.png";
import dotor from "../../../assets/Images/dotor.png";

function InOut() {
    return (
        <Grid container>
            <Grid item xs={12} sm={6}>
                <div
                    style={{
                        backgroundImage: `url(${wow2})`,
                        backgroundPosition: "center",
                        backgroundSize: "cover",
                        width: "100%",
                        height: "50vh",
                    }}
                ></div>
            </Grid>
            <Grid item xs={12} sm={6}>
                <Grid
                    container
                    style={{
                        backgroundColor: "#82d900",
                        width: "100%",
                        height: "50vh",
                    }}
                >
                    <Grid
                        item
                        xs={9}
                        sm={8}
                        md={7}
                        lg={6}
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            alignItems: "flex-end",
                            justifyContent: "center",
                        }}
                    >
                        <img
                            alt="gadna"
                            src={gadna}
                            style={{
                                maxWidth: 250,
                                width: "100%",
                                height: "auto",
                            }}
                        ></img>
                        <Button
                            variant="outlined"
                            style={{
                                maxWidth: 150,
                                width: "100%",
                                height: "auto",
                                borderRadius: 25,
                                backgroundColor: "white",
                                marginTop: "10%",
                            }}
                        >
                            Дэлгэрэнгүй
                        </Button>
                    </Grid>
                    <Grid item xs={3} sm={4} md={5} lg={6}></Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} sm={6}>
                <Grid
                    container
                    style={{
                        backgroundColor: "#2c3d51",
                        width: "100%",
                        height: "50vh",
                    }}
                >
                    <Grid item xs={3} sm={4} md={5} lg={6}></Grid>
                    <Grid
                        item
                        xs={9}
                        sm={8}
                        md={7}
                        lg={6}
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            alignItems: "flex-start",
                            justifyContent: "center",
                        }}
                    >
                        <img
                            alt="dotor"
                            src={dotor}
                            style={{
                                maxWidth: 250,
                                width: "100%",
                                height: "auto",
                            }}
                        ></img>
                        <Button
                            variant="outlined"
                            style={{
                                maxWidth: 150,
                                width: "100%",
                                height: "auto",
                                borderRadius: 25,
                                backgroundColor: "white",
                                marginTop: "10%",
                            }}
                        >
                            Дэлгэрэнгүй
                        </Button>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} sm={6}>
                <div
                    style={{
                        backgroundImage: `url(${wow1})`,
                        backgroundPosition: "center",
                        backgroundSize: "cover",
                        width: "100%",
                        height: "50vh",
                    }}
                ></div>
            </Grid>
        </Grid>
    );
}

export default InOut;
