import React from "react";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { Button, Grid, Paper, useMediaQuery } from "@material-ui/core";

import departmentLogo from "../../../assets/Images/web.png";

import DepartmentsData from "./Data/DepartmentsData";

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: "blue",
    },
    container: {
        minHeight: 300,
        height: "40vh",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
    },

    card: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        backgroundColor: "#d0f6eb",
        width: "100%",
        height: "100%",
        boxShadow: "none",
    },

    column: {
        display: "flex",
        flexDirection: "column",
    },
}));

const Salbar = (props) => {
    const classes = useStyles();
    const data = props.data;

    return (
        <Paper className={classes.card} style={{ flexDirection: "column" }}>
            <h2 style={{ color: "#37b893" }}>{data.title}</h2>
            <div
                style={{
                    backgroundColor: "#37b893",
                    maxWidth: 200,
                    width: "70%",
                    padding: 10,
                    borderRadius: 20,
                    textAlign: "center",
                }}
            >
                <div style={{ color: "white" }}>{data.place}</div>
                <div>{data.floor}</div>
            </div>
            <p style={{ color: "#37b893" }}>{data.time}</p>
            <Button
                style={{
                    backgroundColor: "#83d905",
                    borderRadius: 20,
                    color: "white",
                    height: 30,
                }}
                onClick={() =>
                    window.open(
                        `${data.link}`,
                        "_blank" // <- This is what makes it open in a new window.
                    )
                }
            >
                Дэлгэрэнгүй
            </Button>
        </Paper>
    );
};

function Departments() {
    const classes = useStyles();

    const theme = useTheme();
    const tablet = useMediaQuery(theme.breakpoints.up("lg"));
    const mobile = useMediaQuery(theme.breakpoints.up("md"));

    return (
        <div>
            <Grid container style={{ paddingTop: 20, paddingBottom: 20 }}>
                {tablet ? (
                    <>
                        <Grid item sm={2} style={{ paddingRight: 10 }}>
                            <Paper className={classes.card}></Paper>
                        </Grid>
                        <Grid item sm={8}>
                            <Grid
                                container
                                spacing={1}
                                style={{ paddingTop: 5, paddingBottom: 5 }}
                            >
                                {DepartmentsData.map((item) => {
                                    if (item.id === 1) {
                                        return (
                                            <Grid
                                                item
                                                lg={4}
                                                className={classes.container}
                                                key={item.id}
                                            >
                                                <Salbar data={item} />
                                            </Grid>
                                        );
                                    }
                                })}
                                <Grid item lg={4} className={classes.container}>
                                    <Paper
                                        className={classes.card}
                                        style={{
                                            zIndex: 1,
                                            boxShadow:
                                                "15px 15px 20px 0px rgba(0, 0, 0, 0.3)",
                                            backgroundColor: "white",
                                            alignItems: "flex-end",
                                            paddingBottom: "8%",
                                        }}
                                    >
                                        <img
                                            alt="gadna"
                                            src={departmentLogo}
                                            style={{
                                                maxWidth: 250,
                                                width: "100%",
                                                height: "auto",
                                            }}
                                        ></img>
                                    </Paper>
                                </Grid>
                                {DepartmentsData.map((item) => {
                                    if (item.id !== 1) {
                                        return (
                                            <Grid
                                                item
                                                lg={4}
                                                className={classes.container}
                                                key={item.id}
                                            >
                                                <Salbar data={item} />
                                            </Grid>
                                        );
                                    }
                                })}
                            </Grid>
                        </Grid>
                        <Grid item sm={2} style={{ paddingLeft: 10 }}>
                            <Paper className={classes.card}></Paper>
                        </Grid>
                    </>
                ) : mobile ? (
                    <>
                        <Grid item sm={2} style={{ paddingRight: 10 }}>
                            <Paper className={classes.card}></Paper>
                        </Grid>
                        <Grid item sm={8}>
                            <Grid
                                container
                                spacing={1}
                                style={{ paddingTop: 5, paddingBottom: 5 }}
                            >
                                <Grid
                                    item
                                    sm={12}
                                    lg={4}
                                    className={classes.container}
                                >
                                    <Paper
                                        className={classes.card}
                                        style={{
                                            zIndex: 1,
                                            boxShadow:
                                                "0px 35px 100px -57px rgba(0,0,0,0.75)",
                                            backgroundColor: "white",
                                            alignItems: "flex-end",
                                            paddingBottom: "5%",
                                        }}
                                    >
                                        <img
                                            alt="gadna"
                                            src={departmentLogo}
                                            style={{
                                                maxWidth: 250,
                                                width: "100%",
                                                height: "auto",
                                            }}
                                        ></img>
                                    </Paper>
                                </Grid>
                                {DepartmentsData.map((item) => {
                                    return (
                                        <Grid
                                            item
                                            sm={item.id === 5 ? 12 : 6}
                                            lg={4}
                                            className={classes.container}
                                            key={item.id}
                                        >
                                            <Salbar data={item} />
                                        </Grid>
                                    );
                                })}
                            </Grid>
                        </Grid>
                        <Grid item sm={2} style={{ paddingLeft: 10 }}>
                            <Paper className={classes.card}></Paper>
                        </Grid>
                    </>
                ) : (
                    <>
                        <Grid item xs={12} sm={12} style={{ paddingRight: 10 }}>
                            <Paper className={classes.card}></Paper>
                        </Grid>
                        <Grid item xs={12} sm={12}>
                            <Grid
                                container
                                spacing={1}
                                style={{ paddingTop: 5, paddingBottom: 5 }}
                            >
                                <Grid
                                    item
                                    xs={12}
                                    sm={12}
                                    lg={4}
                                    className={classes.container}
                                >
                                    <Paper
                                        className={classes.card}
                                        style={{
                                            zIndex: 1,
                                            boxShadow:
                                                "0px 25px 100px -57px rgba(0,0,0,0.75)",
                                            backgroundColor: "white",
                                            alignItems: "flex-end",
                                            paddingBottom: "5%",
                                        }}
                                    >
                                        <img
                                            alt="gadna"
                                            src={departmentLogo}
                                            style={{
                                                maxWidth: 250,
                                                width: "100%",
                                                height: "auto",
                                            }}
                                        ></img>
                                    </Paper>
                                </Grid>
                                {DepartmentsData.map((item) => {
                                    return (
                                        <Grid
                                            item
                                            xs={12}
                                            sm={item.id === 5 ? 12 : 6}
                                            lg={4}
                                            className={classes.container}
                                            key={item.id}
                                        >
                                            <Salbar data={item} />
                                        </Grid>
                                    );
                                })}
                            </Grid>
                        </Grid>
                        <Grid item xs={12} sm={12} style={{ paddingLeft: 10 }}>
                            <Paper className={classes.card}></Paper>
                        </Grid>
                    </>
                )}
            </Grid>
        </div>
    );
}

export default Departments;
