import React, { useState } from "react";
import { useTheme } from "@material-ui/core/styles";
import {
  Grid,
  AppBar,
  Toolbar,
  CssBaseline,
  Fab,
  useMediaQuery,
  Link,
} from "@material-ui/core";

// Logo

import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import MenuIcon from "@material-ui/icons/Menu";

// Function

import ScrollTop from "./function/ScrollTop";
import HideOnScroll from "./function/HideOnScroll";
import MobileHeader from "./function/MobileHeader";
import history from "./history";

// Image

import oimlogo from "../../../assets/Images/oim_logo.png";

function Header(props) {
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.up("md"));
  const mobile = useMediaQuery(theme.breakpoints.up("sm"));

  const preventDefault = (event) => event.preventDefault();
  const [mobileMenu, setMobileMenu] = useState(false);

  return (
    <React.Fragment>
      <CssBaseline />
      <HideOnScroll {...props}>
        <AppBar position="fixed">
          <Toolbar
            style={{
              background: "white",
              height: "9vh",
            }}
          >
            {tablet ? (
              <Grid container spacing={3}>
                <Grid item xs={2}></Grid>
                <Grid
                  item
                  xs={2}
                  style={{
                    display: "flex",
                    justifyContent: "flex-start",
                    alignItems: "center",
                  }}
                >
                  <img
                    src={oimlogo}
                    alt="logo"
                    style={{
                      height: "90%",
                      cursor: "pointer",
                    }}
                    onClick={() => history.push(`/`)}
                  />
                </Grid>
                <Grid item xs={1}></Grid>
                <Grid
                  item
                  xs={6}
                  style={{
                    width: "80%",
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                    textAlign: "center",
                  }}
                >
                  <Link
                    onClick={() => history.push(`/`)}
                    style={{ color: "black", cursor: "pointer" }}
                  >
                    Нүүр хуудас
                  </Link>
                  <Link
                    onClick={() => history.push(`/ProductScreen`)}
                    style={{
                      color: "black",
                      fontFamily: "",
                      cursor: "pointer",
                    }}
                  >
                    Бүтээгдэхүүн
                  </Link>
                  <Link
                    onClick={preventDefault}
                    style={{ color: "black", cursor: "pointer" }}
                  >
                    Үйлчилгээ
                  </Link>
                  <Link
                    onClick={preventDefault}
                    style={{ color: "black", cursor: "pointer" }}
                  >
                    Бидний тухай
                  </Link>
                  <Link
                    onClick={preventDefault}
                    style={{
                      color: "black",
                      cursor: "pointer",
                    }}
                  >
                    Салбар
                  </Link>
                  <div>
                    <FacebookIcon
                      style={{
                        fill: "#82d900",
                        cursor: "pointer",
                      }}
                      onClick={() =>
                        window.open(
                          `https://www.facebook.com/Oim.Nogoon.Urlan`,
                          "_blank"
                        )
                      }
                    />
                    <InstagramIcon
                      style={{
                        fill: "#82d900",
                        cursor: "pointer",
                      }}
                      onClick={() =>
                        window.open(
                          `https://www.instagram.com/oim_nogoon_urlan/`,
                          "_blank"
                        )
                      }
                    />
                  </div>
                </Grid>
                <Grid item xs={1}></Grid>
              </Grid>
            ) : mobile ? (
              <Grid container spacing={3}>
                <Grid item xs={1}></Grid>
                <Grid
                  item
                  xs={2}
                  style={{
                    display: "flex",
                    justifyContent: "flex-start",
                    alignItems: "center",
                  }}
                >
                  <img
                    src={oimlogo}
                    alt="logo"
                    style={{
                      height: "90%",
                      cursor: "pointer",
                    }}
                    onClick={() => history.push(`/`)}
                  />
                </Grid>
                <Grid item xs={2}></Grid>
                <Grid
                  item
                  xs={6}
                  style={{
                    width: "85%",
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                    textAlign: "center",
                  }}
                >
                  <Link onClick={preventDefault} style={{ color: "black" }}>
                    Нүүр хуудас
                  </Link>
                  <Link
                    onClick={() => history.push(`/ProductScreen`)}
                    style={{ color: "black" }}
                  >
                    Бүтээгдэхүүн
                  </Link>
                  <Link onClick={preventDefault} style={{ color: "black" }}>
                    Үйлчилгээ
                  </Link>
                  <MenuIcon
                    style={{ fill: "black" }}
                    onClick={() => setMobileMenu(!mobileMenu)}
                  />
                  <MobileHeader mobileMenu={mobileMenu} />
                </Grid>
                <Grid item xs={1}></Grid>
              </Grid>
            ) : (
              <Grid container spacing={3}>
                <Grid
                  item
                  xs={4}
                  style={{
                    display: "flex",
                    justifyContent: "flex-start",
                    alignItems: "center",
                  }}
                >
                  <img
                    src={oimlogo}
                    alt="logo"
                    style={{
                      height: "70%",
                      cursor: "pointer",
                    }}
                    onClick={() => history.push(`/`)}
                  />
                </Grid>
                <Grid item xs={2}></Grid>
                <Grid
                  item
                  xs={6}
                  style={{
                    width: "100%",
                    display: "flex",
                    justifyContent: "flex-end",
                    alignItems: "center",
                    textAlign: "center",
                  }}
                >
                  <MenuIcon
                    style={{ fill: "black" }}
                    onClick={() => setMobileMenu(!mobileMenu)}
                  />
                  <MobileHeader mobileMenu={mobileMenu} />
                </Grid>
              </Grid>
            )}
          </Toolbar>
        </AppBar>
      </HideOnScroll>
      <Toolbar id="back-to-top-anchor" />
      <ScrollTop {...props}>
        <Fab
          style={{ background: "#82d900" }}
          size="small"
          aria-label="scroll back to top"
        >
          <ArrowUpwardIcon />
        </Fab>
      </ScrollTop>
    </React.Fragment>
  );
}

export default Header;
