import React from "react";
import { useTheme } from "@material-ui/core/styles";
import {
    Slide,
    Grid,
    useMediaQuery,
    Link,
    useScrollTrigger,
} from "@material-ui/core";
import PropTypes from "prop-types";
import history from "../history";

// Icon

import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";

function MobileHeader(props) {
    const isMobile = props.mobileMenu;
    const { window } = props;

    const preventDefault = (event) => event.preventDefault();

    const theme = useTheme();
    const tablet = useMediaQuery(theme.breakpoints.up("md"));
    const mobile = useMediaQuery(theme.breakpoints.up("sm"));

    const trigger = useScrollTrigger({ target: window ? window() : undefined });

    if (tablet === false && mobile === true) {
        return (
            <Slide
                appear={false}
                direction="left"
                in={isMobile ? (trigger ? false : true) : false}
                style={{ top: "9vh", right: 0 }}
            >
                <Grid
                    container
                    style={{
                        position: "absolute",
                        backgroundColor: "white",
                    }}
                >
                    <Grid item xs={12}>
                        <Link
                            href="#"
                            onClick={preventDefault}
                            style={{ color: "black" }}
                        >
                            Бидний тухай
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link
                            href="#"
                            onClick={preventDefault}
                            style={{ color: "black" }}
                        >
                            Салбар
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <FacebookIcon
                            style={{
                                fill: "#82d900",
                                cursor: "pointer",
                            }}
                            onClick={() =>
                                window.open(
                                    `https://www.facebook.com/Oim.Nogoon.Urlan`,
                                    "_blank"
                                )
                            }
                        />
                        <InstagramIcon
                            style={{
                                fill: "#82d900",
                                cursor: "pointer",
                            }}
                            onClick={() =>
                                window.open(
                                    `https://www.instagram.com/oim_nogoon_urlan/`,
                                    "_blank"
                                )
                            }
                        />
                    </Grid>
                </Grid>
            </Slide>
        );
    } else if (tablet === false && mobile === false) {
        return (
            <Slide
                appear={false}
                direction="left"
                in={isMobile ? (trigger ? false : true) : false}
                style={{ top: "8.5vh", right: 0 }}
            >
                <Grid
                    container
                    style={{
                        position: "absolute",
                        backgroundColor: "white",
                    }}
                >
                    <Grid item xs={12}>
                        <Link
                            href="#"
                            onClick={() => history.push(`/`)}
                            style={{ color: "black" }}
                        >
                            Нүүр хуудас
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link
                            href="#"
                            onClick={() => history.push(`/ProductScreen`)}
                            style={{ color: "black" }}
                        >
                            Бүтээгдэхүүн
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link
                            href="#"
                            onClick={preventDefault}
                            style={{ color: "black" }}
                        >
                            Үйлчилгээ
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link
                            href="#"
                            onClick={preventDefault}
                            style={{ color: "black" }}
                        >
                            Бидний тухай
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <Link
                            href="#"
                            onClick={preventDefault}
                            style={{ color: "black" }}
                        >
                            Салбар
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <FacebookIcon
                            style={{
                                fill: "#82d900",
                                cursor: "pointer",
                            }}
                            onClick={() =>
                                window.open(
                                    `https://www.facebook.com/Oim.Nogoon.Urlan`,
                                    "_blank"
                                )
                            }
                        />
                        <InstagramIcon
                            style={{
                                fill: "#82d900",
                                cursor: "pointer",
                            }}
                            onClick={() =>
                                window.open(
                                    `https://www.instagram.com/oim_nogoon_urlan/`,
                                    "_blank"
                                )
                            }
                        />
                    </Grid>
                </Grid>
            </Slide>
        );
    } else return null;
}

MobileHeader.propTypes = {
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window: PropTypes.func,
};

export default MobileHeader;
