import React from "react";

//Components

import Carousel from "./Components/Carousel";
import InOut from "./Components/InOut";
import Departments from "./Components/Departments";
import MiniCarousel from "./Components/MiniCarousel";

//data
import MiniCarouselData1 from "./Components/Data/MiniCarouselData";
import MiniCarouselData2 from "./Components/Data/MiniCarouselData2";

function index() {
    return (
        <div>
            <Carousel />
            <MiniCarousel data={MiniCarouselData2} />
            <InOut />
            <Departments />
            <MiniCarousel data={MiniCarouselData1} />
        </div>
    );
}

export default index;
